/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { AiOutlineRight } from 'react-icons/ai';

export default function CountDown({ style, totalTime, onClick, dir }) {
    // const { dir = 'down', style = {}, onClick = () => {}, time } = props;
    let time = totalTime;
    const [sec, setSec] = React.useState('00');
    const [min] = React.useState('00');

    const timify = (t) => {
        t /= 1000;
        if (t >= 0) {
            if (t > 60) {
                t = '00';
            }
            if (t <= 9) {
                t = `0${t}`;
            }
            return t;
        }
        return '00';
    };

    React.useEffect(() => {
        setSec(timify(time))
        timer();
    }, []);

    const timer = () => {
        setInterval(() => {
            if (time >= 0) {
                if (dir === 'down') {
                    time -= 1000;
                }
                if (dir === 'up') {
                    time += 1000;
                }
                setSec(timify(time));
            }
        }, 1000);
    };

    const resetTimer = () => {
        setInterval(() => {
            if (time >= 0) {
                if (dir === 'down') {
                    time -= 1000;
                }
                if (dir === 'up') {
                    time += 1000;
                }
                setSec(timify(time));
            }
        }, 1000);
    };

    if (`${min}:${sec}` !== '00:00') {
        return (
            <div style={{ fontSize: "14px", fontWeight: "400", color: "#333333", marginTop: "6%" }}>Resend in {`${min}:${sec}`}</div>
        );
    }
    return (
        <div
            className='header2dark secondaryGreen'
            style={{ marginTop: "6%" }}
            onClick={() => {
                resetTimer();
                onClick();
            }}
        >
            Resend OTP <AiOutlineRight style={{ verticalAlign: "unset" }} size={10} />
        </div>
    );
}
