import React from 'react';
import Spinner from 'react-bootstrap/Spinner';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import AdminServices from 'services/api/AdminServices';
import CountDown from 'views/mobileView/components/atoms/CountDown';
import AuthHeader from 'views/mobileView/components/molecules/Headers/AuthHeader';
import { browserName, deviceType, mobileModel, osName, osVersion } from 'react-device-detect';
import db from 'libs/db';

export default function VerifyOTP2(props) {

	const [otp, setOtp] = React.useState('');
	const [invalidOTP, setinvalidOTP] = React.useState(false);
	const [mainOTP, setmainOTP] = React.useState('');
	const [nextBtnPressed, setNextBtnPressed] = React.useState(false)

	const { state } = props.location
	// const state = {verify:"EMAIL",name:'Omkar',email:"obhogle@gmail.com",mobile:"9768928535",otp:"123456"}
	// const state = {verify:"MOBILE",name:'Omkar',email:"obhogle@gmail.com",mobile:"9768928535",otp:"123456"}

	React.useEffect(async () => {
		if (state === null || state === undefined) {
			props.history.push('/signup')
		} else {
			sendOTP();
		}
	}, [])

	const validateInput = () => {
		return otp.length === 6
	}

	const onNextBtnPressed = () => {
		if (otp === `${mainOTP}` && mainOTP !== '') {
			createUser(state.mobile)
		} else {
			setinvalidOTP(true);
		}
	}

	const sendOTP = async () => {
		try {
			setmainOTP('')
			const res = await AdminServices.generateOTP(state.verify === 'EMAIL' ? state.email : state.mobile, state.verify).then((res) => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					setmainOTP(res.data.otpcode);
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	};
	const resendOTP = () => {
		setinvalidOTP(false);
		setOtp('')
		sendOTP();
	}
	const createUser = async (mobile = '') => {
		setNextBtnPressed(true);
		const data = {
			mobileNumber: mobile, // will be blank if skip is pressed
			email: state.email,
			clientName: state.name,
			source: deviceType + ":" + browserName + ":" + (mobileModel === "none" ? osName : mobileModel) + ":" + osVersion
		}
		try {
			const res = await AdminServices.createUser(data).then((res) => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					const userData = {
						name: state.name,
						email: state.email,
						mobile: mobile,
						id: res.data.ClientId,
						isLogged: true,
					};
					db.set(db.model.user, JSON.stringify({ userData }));
					props.history.push({
						pathname: '/dashboard',
						state: { ...state }
					})
					setNextBtnPressed(false);
				} else {
					toast.error("Try again ...");
					setNextBtnPressed(false);
				}
			} else {
				toast.error("Try again ...");
				setNextBtnPressed(false);
			}
		} catch (error) {
			toast.error("Try again ...");
			setNextBtnPressed(false);
		}
	}


	return <div className="mainContainer">
		{
			state.verify === 'EMAIL' ?
				<AuthHeader navigation={props.history} left='login' />
				:
				<AuthHeader navigation={props.history} left='login' right="skip" onSkip={createUser} />
		}
		<div className="dataContainer">
			<div className="login-header">
				<div className="bigHeader">Almost there!</div>
				<div className="bodyTextDark" style={{ marginTop: "6px" }}>
					Please enter the OTP received on <span className='underlineGreen'>
						{state.verify === 'EMAIL' ? state.email : state.mobile}</span>
				</div>
			</div>
			<FloatingTextInput
				type="tel"
				id="typepass"
				label="OTP"
				style={{ letterSpacing: '16px' }}
				autoFocus
				onChange={(e) => {
					setinvalidOTP(false)
					const n = e.target.value.replace(/\D/g, '')
					setOtp(n)
				}}
				value={otp}
				maxLength={6}
			/>
			{invalidOTP ? (
				<div className="errorMsg">
					Incorrect OTP. Try again!
				</div>
			) : ''}
			<CountDown totalTime={30000} dir="down" onClick={() => { resendOTP(); }} />
		</div>
		<div className="login-footer">
			{
				nextBtnPressed ?
					<button className="solidButton" style={{ opacity: 0.4 }}>
						<Spinner
							style={{ color: "#fff" }}
							as="span"
							animation="border"
							size="sm"
							role="status"
							aria-hidden="true"
						/>
					</button>
					:
					<button className="solidButton" style={{ opacity: validateInput() ? 1 : 0.4 }}
						onClick={() => { validateInput() && onNextBtnPressed() }}>
						Verify <AiOutlineRight size={12} />
					</button>
			}
		</div>
	</div>
}
